      <b-navbar-brand href="#">
        <router-link class="gs_brand" :to="this.links[0].url">
          <img src="../assets/img/logow.jpg" alt="logo gabrisca" />
          <span>Gabriele</span>
          <span>Scarparo</span>
        </router-link>
      </b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" v-model="showCollapse" is-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
