                  {{ link.text }}
                </router-link>
              </li>
              <b-nav-item-dropdown class="projects" text="projects" right>
                <li
                  v-for="(project, index) in projects"
                  :key="index"
                  @click.stop=""
                  :class="{ active: project.current }"
                  class="my_drop"
                >
                  <router-link :to="project.url">
                    {{ project.text }}
                  </router-link>
                </li>
              </b-nav-item-dropdown>
            </ul>
          </b-navbar-nav>
        </b-navbar-nav>
      </b-collapse>
