<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "HeaderComp",
  data() {
    return {
      showCollapse: false,
      links: [
        {
          text: "home",
          url: "/",
          current: false,
        },
        {
          text: "about",
          url: "/about",
          current: false,
        },
      ],
      projects: [
        {
          text: "home projects",
          url: "/projects",
          current: false,
        },
        {
          text: "bookshock",
          url: "/projects/bookshock",
          current: false,
        },
        {
          text: "boolflix",
          url: "/projects/boolflix",
          current: false,
        },
        {
          text: "weatherdb",
          url: "/projects/weatherdb",
          current: false,
        },
      ],
    };
  },
  watch: {
    $route() {
      // This will close the collapse if any part of the route changes
      // including query, params, hash, name, or path
      this.showCollapse = false;
    },
  },
};
</script>
