<script>
export default {
  name: "Jumbotron2Comp",
  components: {},
  data() {
    return {
      morning: false,
      afternoon: false,
      evening: false,
      welcome: false,
    };
  },
  methods: {
    currentDate() {
      const current = new Date();
      switch (current.getDay()) {
        case 0:
          return `Sunday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 1:
          return `Monday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 2:
          return `Tuesday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 3:
          return `Wednesday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 4:
          return `Thursday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 5:
          return `Friday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 6:
          return `Saturday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        default:
          "";
          break;
      }
    },
    greeting() {
      var hours = new Date().getHours();
      //console.log(hours);
      if (hours >= 6 && hours < 12) {
        this.morning = true;
        return "Good Morning";
      } else if (hours >= 12 && hours < 18) {
        this.afternoon = true;
        return "Good Afternoon";
      } else if (hours >= 18 && hours < 23) {
        this.evening = true;
        return "Good Evening";
      } else {
        this.welcome = true;
        return "Welcome";
      }
    },
  },
  created() {
    setTimeout(this.typeText, this.newTextDelay + 200);
  },
};
</script>

